//  css 
/**
 CSSStyleDeclaration.setProperty()      
   CSS.
 */
export const getCssVariables = (theme) => {
  //     root- css,   
  const root = document.querySelector(":root");
  // console.log(root.style);

  root.style.setProperty(
    `--theme-default-bgimage`,
    ` var(--theme-${theme}-bgimage)`
  );
};
